import React, { Component } from 'react';
import {Route} from 'react-router-dom';
import axios from 'axios'
import Callback from './Callback';
import { Redirect } from 'react-router-dom'
import {
    BrowserRouter as Router,
    Switch,
    Link
  } from "react-router-dom";

import Cookies from 'universal-cookie';
import Search from 'react-search'
import './Ranking.css';
import {refresh_token} from './Api'
class Ranking extends React.Component {
    constructor(props)
    {
    super(props);
    const list=[];
    this.state = {
        object:[],
        problemwiseresult:[],
        clicked:"",
        institution:"",
        problemscore:[],
        problemcodes:[],
        problemlist:[],
        usernaam:"",
        country:'',
        institution:'',
        

    
    }
    this.ranking=this.ranking.bind(this);
     
    
    }
    onCountry = (e) => {
      var country=this.state.country;
      const value = e.target.value;
          console.log(value.length);
          let suggestions:[];
          if (value.length > 0)
          {
            this.setState(()=> ({country:value}));
            console.log(this.state.taxt)
          }
          else
          {
            this.setState(() => ({country:value}));
          }
         }

         onusername = (e) => {
          var username=this.state.usernaam;
          const value = e.target.value;
             
                this.setState(() => ({usernaam:value}));
          
             }
             oninstitution = (e) => {
              var isnstituion=this.state.institution;
              const value = e.target.value;
                 
                    this.setState(() => ({institution:value}));
              
                 }
    

             onCountry = (e) => {
              var country=this.state.country;
              const value = e.target.value;
                  console.log(value.length);
                  let suggestions:[];
                  if (value.length > 0)
                  {
                    this.setState(()=> ({country:value}));
                    console.log(this.state.taxt)
                  }
                  else
                  {
                    this.setState(() => ({country:value}));
                  }
                 }
        



    getScore=(pass1)=>{
      var marks=[],p,q;
        for(p=0;p<this.state.problemcodes.length;p++){
            for(q=0;q<pass1.problemScore.length;q++){
                if(pass1.problemScore[q].problemCode==this.state.problemcodes[p]){
                  marks.push(pass1.problemScore[q].score);
                  break;
                }
             }
             if(q==pass1.problemScore.length) marks.push(0);
        }
        return marks.map((pass2,p2)=> <td key={p2}>{pass2}</td>)
  }
   
    ranking = () =>
    {

        const cookies = new Cookies();
        var p=0;
        var token1=cookies.get('username');
        var value1=cookies.get('contestcode');
        var value=cookies.get('problemcode');
        var country=this.state.country;
        var username=this.state.usernaam;
        var institution=this.state.institution;
       
        console.log(country);
        console.log(username);
        console.log(institution);
      //  var ram="username"
        let object=null;
        axios.get(`https://api.codechef.com/rankings/${value1}?`+`fields=`+`&country=`+country+`&institution=`+institution+`&institutionType=&offset=`+username+`&limit=&sortBy=&sortOrder=`,
        {headers: {'Content-Type': 'application/json',
              'Accept': 'application/json',
            'Authorization': 'Bearer '+ token1 } }
           ) .then(res => {
            var pcodes=(res.data.result.data.content["0"].problemScore.map((item)=>item.problemCode.toString()));   
            this.setState({object:res.data.result.data.content,problemwiseresult:pcodes})})
      .catch(function (error)
      {
        console.log("hii");
        console.log(error.message);
      });
      const array=this.state.object;
     
        console.log(this.state.object);
        
    }
    contestdetails=()=>
    {
      const cookies = new Cookies();

  var token1=cookies.get('username');
  var value=cookies.get('contestcode');

      axios.get(`https://api.codechef.com/contests/${value}`,
      { headers: {'Content-Type': 'application/json',
            'Accept': 'application/json',
          'Authorization': 'Bearer '+ token1 } }
    ) .then(res => {  
      this.setState( {problemlist:res.data.result.data.content.problemsList})})
         .catch(function (error)
    {
      console.log(error.message);
    });
    console.log(this.state.problemlist)
      if(this.state.problemlist.length>0)
      {
        console.log(this.state.problemlist.length);
        console.log(this.state.problemlist)
        var pCode=this.state.problemlist.map((item)=>item.problemCode)
          
            this.setState({problemcodes:pCode});
            console.log(this.state.problemcodes.length);
            this.setState({clicked:"Yes"});
           
      }

    }
    logout = (e)=>
    {
     
      console.log(this.state.clicked)
      const cookies=new Cookies();
      cookies.remove('username',{path:'/'});
      cookies.remove('refresh_token',{path:'/'});
      cookies.remove('visited',{path:'/'});
      this.setState({clicked:"logout"})
      
    }
      render(){
        refresh_token();
        var country=this.state.country;
        var username=this.state.usernaam;
        var institution=this.state.institution;
        
        const cookies = new Cookies();
        var isitstart =cookies.get('isitstart');
        if(this.state.clicked=="logout")
        {
          return <Redirect to="/" />
        }
        if(this.state.clicked=="Yes")
        {
           
          return (
            <div>
            <div>
		
            <button onClick={this.logout} className="butoon3">Logout</button>
            <input type="text" className="input101" value={username} onChange={this.onusername}  placeholder="Enter Offset"/>
            <input type="text"  className="input101" value={country} onChange={this.onCountry}  placeholder="Enter Country"/>
            <input type="text" className="input101" value={institution} onChange={this.oninstitution}  placeholder="Enter Instituionname"/>
          <button  onClick={this.ranking} className="rankingbutton1">
							Apply
						</button>
            
           
				
               <div className="respTable">
              <table className="p">
                    <tbody>
                        <tr className="pppp">
                            <th className="hh"><b>RANK</b></th>
                            <th className="hh"><b>USERNAME</b></th>
                            <th className="hh"><b>TOTAL SCORE</b></th>
                            {this.state.problemcodes.map((item,i)=> <th className="hh" key={i}>{item}</th>)}
                        </tr>
                       
                        {this.state.object.map((item,i)=>
                            <tr className="pppp" key={i}>
                                <td >{item.rank.toString()}</td>
                                <td><a href={"https://www.codechef.com/users/"+item.username+"/"} target="_blank">{item.username}</a></td>
                                <td>{item.totalScore}</td>
                                {this.getScore(item)}
                            </tr>
                           
                        )}
                    </tbody>
                    
                </table>
                 
            </div>
            </div>







           
            </div>


          )
        }
        if(isitstart=="no")
        {
          return <div><h1>Contest is not started yet. Go to contest page </h1></div>
        }
        
        return (
          <div>
             {this.ranking()}
           {this.contestdetails()}
          </div>
        );
      }
    }
    export default Ranking;
    