import React from 'react';
import ReactDOM from 'react-dom';

import './index.css';

import Api from './Api';
import Login from './Login';
import * as serviceWorker from './serviceWorker';
import { CookiesProvider } from 'react-cookie';
import {BrowserRouter,HashRouter,Route,Redirect,Switch} from 'react-router-dom';
import Contest from './Contest'
import Problem from './Problem'
import Problemdetails from './Problemdetails'
import Ranking from './Ranking'
import Submit from './Submit'
import User from './User'
import Activity from './Activity'
import Divison from './Divison'
let initialStore = { 
    token:"",
};


ReactDOM.render(
 <CookiesProvider>	
  <HashRouter>
   
    
    <Switch>
    	<Route path="/" exact component={Api} />
		<Route path="/frontend/ide/" exact component={Submit} />
		<Route path="/contest/divison/" exact component={Divison} />
		<Route path="/ranking/:id3/" exact component={User}></Route>
		<Route path="/:id4/activity/" exact component={Activity}></Route>
    	<Route path='/contest/' exact component={Contest}></Route> 
		<Route path='/contest/ranking/' exact component={Ranking}></Route> 
			<Route path="/contest/problem/" exact component={Problem}></Route>
		<Route path="/contest/problem/:id/:id1/" exact component={Submit}></Route>
		<Route path="/contest/problem/:id/"  exact component={Problemdetails}></Route>
		
   	</Switch>
   	</HashRouter>
 </CookiesProvider>
, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();